import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router';
import { ReactComponent as ArrowLeft } from '../assets/arrow-left.svg';

const NotePage = () => {
    let navigate = useNavigate();
    const [note, setNote] = useState('');
    let params = useParams();
    const id = params.id;

    useEffect(() => {
        getNote();
    }, []);

    const getNote = async () => {
        let res = await fetch(`https://polar-escarpment-96937.herokuapp.com/api/notes/${id}/`);
        let data = await res.json();
        setNote(data.body);
    };

    const updateNote = async () => {
        await fetch(`http://127.0.0.1:8000/api/notes/${id}/`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ body: note }),
        });
    };

    async function handleClick() {
        await updateNote();
        console.log(note);
        navigate('/dj_notes_app/');
    }

    async function deleteNote() {
        await fetch(`https://polar-escarpment-96937.herokuapp.com/api/notes/${id}/`, {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
            },
        });
        navigate('/dj_notes_app/')
    }
    return (
        <div className="note">
            <div className="note-header">
                <h3>
                    <ArrowLeft
                        onClick={
                            note ? () => handleClick() : () => deleteNote()
                        }
                    />
                </h3>

                <button onClick={deleteNote}>Delete</button>
            </div>
            <textarea
                value={note}
                onChange={(e) => setNote(e.target.value)}
            ></textarea>
        </div>
    );
};

export default NotePage;
